@import './scss/typography.scss';

.container {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.topBar {
    display: flex;
    justify-content: space-between;
    
    border-bottom: $panel-border;
    
    h2 {
        display: flex;
        align-items: center;
        padding: 10px;
    }
}

.body {
    display: flex;
    height: 100%;
    overflow: hidden;
}
